@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@300;400;500;600&display=swap);
header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}


/* =================== CTA Style =================== */

.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    justify-content: center;
}


/* =================== Socials Style =================== */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary-varient);
}


/* =================== Image Style =================== */

.me {
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 10.8rem);
    margin-top: 0.5rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 1rem 1.5rem 1.5rem 1.5rem;
}


/* =================== Scroll Style =================== */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    color: var(--color-primary-varient);
}


/* =========================== Media Queries (Medium) =========================== */
@media screen and (max-width: 1024px) {
    
    header {
            height: 68vh;
        }
    

    
}

/* =========================== Media Queries (Small) =========================== */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials, .scroll__down{
        display: none;
    }
}
nav {
    background: black;
    width: -webkit-max-content;
    width: max-content;
    display: flex;
    padding: 0.7rem 1.7rem;
    border: 1px solid var(--color-primary);
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-primary-varient);
    font-size: 1.2rem;
}

nav a:hover {
    background: var(--color-primary);
    font-size: 1.4rem;
}

nav a.active {
    border: 1px solid var(--color-primary);
    color: var(--color-light);
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: var(--color-primary);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-primary);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align-last: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-bg-varient);
    cursor: default;
    color: var(--color-primary-varient);
}

.about__card:hover .about__icon{
    color: var(--color-primary-varient);
    font-size: 1.6rem;
}

.about__icon {
    color: var(--color-white);
    font-size: 1.4rem;
    margin-bottom: 1rem;
    transition: var(--transition);
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 00.75rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
    text-indent: 50px;
}


/* =========================== Media Queries (Medium) =========================== */
@media screen and (max-width: 1024px) {
    
   .about__container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
   }
    
   .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
   }

   .about__content p {
    margin: 1rem 0 1.5rem;
   }
}

/* =========================== Media Queries (Small) =========================== */
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
       }
    
    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-primary);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
    color: var(--color-primary-varient);
}

.experience__container > div:hover .experience__details-icon {
    font-size: 1.1rem;
    color: var(--color-primary);
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 0.5fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    transition: var(--transition);
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    transition: var(--transition);
}


/* =========================== Media Queries (Medium) =========================== */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content {
        padding: 1rem;
    }
 }
 
 /* =========================== Media Queries (Small) =========================== */
 @media screen and (max-width: 600px) {
     .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
     }

     .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
     }
 }
.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.portfolio__item {
    background: var(--color-primary);
    padding: 1rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    align-content: center;
}

.portfolio__item:hover {
    border-color: var(--color-primary);
    background: transparent;
}

.portfolio__item-image {
    border-radius: 1.5rem;
    border: 1px solid black;
    overflow: hidden;
    height: 300px;
    width: 300px;
    margin: auto;
}

.portfolio__item h3 {
    margin: 1.2rem 0 1.2rem;
}

.portfolio__item h5 {
    text-indent: 50px;
}

.portfolio__item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 1.2rem;
    margin-bottom: -0.5rem;
    justify-content: center;
}

#github {
    background: black !important;
    transition: var(--transition);
}

.portfolio__item:hover #github {
    background: var(--color-primary-varient) !important;
    color: var(--color-white);
}

#github:hover {
    font-size: 1.1rem;
}



/* =========================== Media Queries (Medium) =========================== */
@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
 }
 
 /* =========================== Media Queries (Small) =========================== */
 @media screen and (max-width: 600px) {
     .portfolio__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
     }
 }



.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
    margin-bottom: 10rem;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option {
    background: var(--color-primary);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
    color: var(--color-white);
}

.contact__option #link {
    color: var(--color-white);
    margin-top: 0.7rem;
    display: inline-block;
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary);
    color: var(--color-primary-varient);
}

.contact__option:hover #link {
    color: var(--color-primary-varient);
}

.contact__option #link:hover {
    color: var(--color-white);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

#send {
    background: black ;
    transition: var(--transition);
}

#send:hover {
    background: var(--color-primary-varient) ;
    color: var(--color-white);
    font-size: 0.9rem;

}




/* ============================= FORM ============================= */


form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary);
    resize: none;
    color: var(--color-white);
}


/* =========================== Media Queries (Medium) =========================== */
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
 }
 
 /* =========================== Media Queries (Small) =========================== */
 @media screen and (max-width: 600px) {
     .container.contact__container {
        width: var(--container-width-sm);
     }
 }


* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #000000;
    --color-bg-varient: #6060e7;
    --color-primary: #373786;
    --color-primary-varient: #6060e7;
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Dosis', sans-serif;
    background: #000000;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
}

/* ================ General Styles ================ */

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    height: 50rem;
}

section > h2, section > h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #6060e7;
    color: var(--color-primary-varient);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a {
    color: #6060e7;
    color: var(--color-primary-varient) ;
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #6060e7;
    color: var(--color-primary-varient);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #6060e7;
    border: 1px solid var(--color-bg-varient);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #6060e7;
    background: var(--color-bg-varient);
    color: #fff;
    color: var(--color-white);
    border-color: transparent;
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/* =========================== Media Queries (Medium) =========================== */
@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* =========================== Media Queries (Small) =========================== */
@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}
